<template>
  <div class="auth-wrapper auth-v2">
    <b-row class="auth-inner m-0">

      <!-- Left Text-->
      <b-col
          lg="8"
          class="d-none d-lg-flex align-items-center p-0"
      >
        <div class="h-100 w-100 cover-image" :style="{ backgroundImage: 'url(' + imgUrl + ')' }">

        </div>
      </b-col>
      <!-- /Left Text-->

      <!-- Register-->
      <b-col
        lg="4"
        class="d-flex align-items-center auth-bg px-2 p-lg-5"
      >
        <b-col
          sm="8"
          md="6"
          lg="12"
          class="px-xl-2 mx-auto d-flex align-items-center flex-column"
        >
          <b-link class="logo">
            <b-img
                :src="appLogoImage"
                alt="logo"
            />
          </b-link>
         <div class="w-100">
           <b-card-title class="mb-1">
             Adventure starts here
           </b-card-title>
           <b-card-text class="mb-2">
             Make your app management easy and fun!
           </b-card-text>

           <!-- form -->
           <validation-observer
               ref="registerForm"
               #default="{invalid}"
           >
             <b-form
                 class="auth-register-form mt-2"
                 @submit.prevent="register"
             >
               <!-- username -->
               <b-form-group
                   label="Username"
                   label-for="register-username"
               >
                 <validation-provider
                     #default="{ errors }"
                     name="Username"
                     vid="username"
                     rules="required"
                 >
                   <b-form-input
                       id="register-username"
                       v-model="username"
                       name="register-username"
                       :state="errors.length > 0 ? false:null"
                       placeholder="johndoe"
                   />
                   <small class="text-danger">{{ errors[0] }}</small>
                 </validation-provider>
               </b-form-group>

               <!-- email -->
               <b-form-group
                   label="Email"
                   label-for="register-email"
               >
                 <validation-provider
                     #default="{ errors }"
                     name="Email"
                     vid="email"
                     rules="required|email"
                 >
                   <b-form-input
                       id="register-email"
                       v-model="userEmail"
                       name="register-email"
                       :state="errors.length > 0 ? false:null"
                       placeholder="john@example.com"
                   />
                   <small class="text-danger">{{ errors[0] }}</small>
                 </validation-provider>
               </b-form-group>

               <!-- password -->
               <b-form-group
                   label-for="register-password"
                   label="Password"
               >
                 <validation-provider
                     #default="{ errors }"
                     name="Password"
                     vid="password"
                     rules="required"
                 >
                   <b-input-group
                       class="input-group-merge"
                       :class="errors.length > 0 ? 'is-invalid':null"
                   >
                     <b-form-input
                         id="register-password"
                         v-model="password"
                         class="form-control-merge"
                         :type="passwordFieldType"
                         :state="errors.length > 0 ? false:null"
                         name="register-password"
                         placeholder="············"
                     />
                     <b-input-group-append is-text>
                       <feather-icon
                           :icon="passwordToggleIcon"
                           class="cursor-pointer"
                           @click="togglePasswordVisibility"
                       />
                     </b-input-group-append>
                   </b-input-group>
                   <small class="text-danger">{{ errors[0] }}</small>
                 </validation-provider>
               </b-form-group>


               <b-form-select
                   v-model="selectedAccType"
                   :options="options"
               />

               <b-form-group>
                 <b-form-checkbox
                     id="register-privacy-policy"
                     v-model="status"
                     name="checkbox-1"
                 >
                   I agree to
                   <b-link>privacy policy & terms</b-link>
                 </b-form-checkbox>
               </b-form-group>

               <b-button
                   variant="primary"
                   block
                   type="submit"
                   :disabled="invalid"
               >
                 Sign up
               </b-button>
             </b-form>
           </validation-observer>

           <p class="text-center mt-2">
             <span>Already have an account?</span>
             <b-link :to="{name:'auth-login'}">
               <span>&nbsp;Sign in instead</span>
             </b-link>
           </p>

           <!-- divider -->
           <div class="divider my-2">
             <div class="divider-text">
               or
             </div>
           </div>

           <div class="auth-footer-btn d-flex justify-content-center">
             <b-button
                 variant="facebook"
                 href="javascript:void(0)"
             >
               <feather-icon icon="FacebookIcon" />
             </b-button>
             <b-button
                 variant="twitter"
                 href="javascript:void(0)"
             >
               <feather-icon icon="TwitterIcon" />
             </b-button>
             <b-button
                 variant="google"
                 href="javascript:void(0)"
             >
               <feather-icon icon="MailIcon" />
             </b-button>
             <b-button
                 variant="github"
                 href="javascript:void(0)"
             >
               <feather-icon icon="GithubIcon" />
             </b-button>
           </div>
         </div>
        </b-col>
      </b-col>
    <!-- /Register-->
    </b-row>
  </div>
</template>

<script>
/* eslint-disable global-require */
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import VuexyLogo from '@core/layouts/components/Logo.vue'
import {
  BRow, BCol, BLink, BButton, BForm, BFormCheckbox, BFormGroup, BFormInput, BInputGroup, BInputGroupAppend, BImg, BCardTitle, BCardText, BFormSelect
} from 'bootstrap-vue'
import { required, email } from '@validations'
import { togglePasswordVisibility } from '@core/mixins/ui/forms'
import store from '@/store/index'
import useJwt from '@/auth/jwt/useJwt'
import {$themeConfig} from "@themeConfig";

export default {
  components: {
    BRow,
    BImg,
    BCol,
    BLink,
    BButton,
    BForm,
    BCardText,
    BCardTitle,
    BFormCheckbox,
    BFormGroup,
    BFormInput,
    BInputGroup,
    BInputGroupAppend,
    BFormSelect,
    // validations
    ValidationProvider,
    ValidationObserver,
  },
  mixins: [togglePasswordVisibility],
  data() {
    return {
      status: '',
      username: '',
      userEmail: '',
      password: '',
      sideImg: require('@/assets/images/pages/register-image.jpg'),
      // validation
      required,
      email,
      selectedAccType: null,
      options: [
        { value: null, text: 'Agents, General Users, Investor Buyers', disabled: true },
        { value: 'Agent', text: 'Agents' },
        { value: 'General User', text: 'General Users' },
        { value: 'Investor Buyer', text: 'Investor Users' },
      ],
    }
  },
  computed: {
    passwordToggleIcon() {
      return this.passwordFieldType === 'password' ? 'EyeIcon' : 'EyeOffIcon'
    },
    imgUrl() {
      return this.sideImg
    },
  },
  methods: {
    register() {
      this.$refs.registerForm.validate().then(success => {
        if (success) {
          useJwt.register({
            username: this.username,
            email: this.userEmail,
            password: this.password,
          })
            .then(response => {
              useJwt.setToken(response.data.accessToken)
              useJwt.setRefreshToken(response.data.refreshToken)
              localStorage.setItem('userData', JSON.stringify(response.data.userData))
              this.$ability.update(response.data.userData.ability)
              this.$router.push('/')
            })
            .catch(error => {
              this.$refs.registerForm.setErrors(error.response.data.error)
            })
        }
      })
    },
  },
  setup() {
    // App Name
    const { appName, appLogoImage } = $themeConfig.app
    return {
      appName,
      appLogoImage,
    }
  },
}
/* eslint-disable global-require */
</script>

<style lang="scss">
@import '@core/scss/vue/pages/page-auth.scss';
</style>
